.PlotContainers-positionRelative {
  position: relative;
}

.PlotContainers-positionAbsolute {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
}

.LocationPlotSelectionLabel-float {
  position: relative;
  width: 100px;
  margin-left: -50px;
  text-align: center;
  color:white;
  font: 14pt Sans;
}

.LocationPlotSelectionLabel {
  position:relative;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.fullscreen {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 9999;
}

.RespiratoryScoresTooltip-RespiratorySupport {
  padding: 5;
  background-color: #fff8ef;
  font-size: 12px;
  white-space: nowrap;
  padding: 5px;
}

.RespiratoryScoresTooltip h1 {
  margin: 0;
  font-size: 16px;
}

.RespiratoryScoresTooltip-RespiratorySupport table {
  width: 100%;
  font-size: 12px;
}

.RespiratoryScoresTooltip-RespiratorySupport table td:nth-child(odd) {
  font-weight: 700;
}

.RespiratoryScoresTooltip {
  pointer-events: none;
  width: 350px;
  padding: 0;
  margin: 0;
  overflow: hidden;
  border: solid 1px black;
  border-radius: 4;
  box-shadow: 2px 2px 2px black;
}

.RespiratoryScoresTooltip-RespiratorySupport table td:nth-child(even) {
  text-align: right;
}

.RespiratoryScoresTooltip-RespiratorySupport small {
  margin-left: 5px;
}

.RespiratoryScoresTooltip-RespiratorySupport-DualTable {
  display: flex;
  justify-content: space-between;
}

.RespiratoryScoresTooltip-RespiratorySupport-DualTable>div {
  width: 50%;
}

.RespiratoryScoresTooltip-LocationTime {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 5px;
}

.RespiratoryScoresTooltip-LocationTime-Location {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.RespiratoryScoresTooltip-LocationTime-Time {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  font-size: 12px;
}

.fullscreen {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 9999;
}

.leftHandle {
  background-color:grey;
  opacity: 0.5;
  cursor:w-resize;
}

.rightHandle {
  background-color:grey;
  opacity: 0.5;
  cursor:e-resize;
}

.mainHandle {
  background-color:lightgrey;
  opacity: 0.5;
  cursor:ew-resize;
}

.topHandle {
  background-color:lightgrey;
  opacity: 0.5;
  cursor:ew-resize;
}

